/* Default Styles for Desktop */
body {
  font-family: 'Roboto', sans-serif;
  background: linear-gradient(to bottom right, #f4f4f4, #e9ecef);
  margin: 0;
  padding: 0;
  color: #333;
}

.App {
  padding: 20px;
}

/* Hamburger Menu Styling */
.hamburger-menu {
  position: relative;
  display: inline-block;
  margin-bottom: 20px;
}

.menu-icon {
  font-size: 30px;
  cursor: pointer;
  padding: 10px;
  background-color: #007bff;
  color: white;
  border-radius: 50%;
  transition: background-color 0.3s;
}

.menu-icon:hover {
  background-color: #0056b3;
}

.menu-content {
  display: none; /* Hide menu content initially */
  position: absolute;
  top: 50px; /* Adjust as needed */
  left: 0;
  background-color: #f4f4f4;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  border-radius: 8px;
  padding: 10px;
  z-index: 1;
}

.hamburger-menu:hover .menu-content {
  display: block; /* Show menu content on hover */
}

.menu-content button {
  display: block;
  width: 100%;
  background-color: #007bff;
  color: white;
  border: none;
  padding: 10px;
  margin: 5px 0;
  border-radius: 8px;
  cursor: pointer;
  font-size: 16px;
  transition: background-color 0.3s;
}

.menu-content button:hover {
  background-color: #0056b3;
}

/* Table Styling */
table {
  width: 100%;
  border-collapse: collapse;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

table, th, td {
  border: 1px solid #ddd;
}

th, td {
  padding: 10px;
  text-align: left;
}

th {
  background-color: #007bff;
  color: #fff;
  font-family: 'Montserrat', sans-serif;
}

td {
  background-color: #fdfdfd;
}

tr:nth-child(even) td {
  background-color: #f7f9fa;
}

/* Highlight entire row based on bet result */
.win-row {
  background-color: #d4edda; /* Light green */
  color: #155724; /* Dark green text */
}

.lose-row {
  background-color: #f8d7da; /* Light red */
  color: #721c24; /* Dark red text */
}

.neutral-row {
  background-color: #fff3cd; /* Light yellow */
  color: #856404; /* Dark yellow text */
}

.default-row {
  background-color: #ffffff; /* White */
  color: #333333; /* Default text color */
}

/* Additional Styling */
h2 {
  color: #007bff;
  font-family: 'Montserrat', sans-serif;
  font-size: 24px;
  text-align: center;
  margin-bottom: 20px;
}

/* Hover effect for table rows */
tr:hover td {
  background-color: #e9ecef;
  transition: background-color 0.2s ease-in-out;
}

/* Summary Table Styling */
.summary-table {
  width: 100%;
  border-collapse: collapse;
  margin-bottom: 20px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  border-radius: 8px;
}

.summary-table th, .summary-table td {
  border: 1px solid #ddd;
  padding: 12px;
  text-align: center;
}

.summary-table th {
  background-color: #343a40;
  color: #fff;
  font-family: 'Montserrat', sans-serif;
}

.positive-row {
  background-color: #c3e6cb; /* Light green */
  color: #155724; /* Dark green text */
}

.negative-row {
  background-color: #f5c6cb; /* Light red */
  color: #721c24; /* Dark red text */
}

/* Ensure clickable rows have pointer cursor */
tr {
  cursor: pointer;
}

tr.positive-row:hover, tr.negative-row:hover {
  background-color: #f1f1f1;
}

/* Styling for hyperlinks in the OC Link column */
a {
  text-decoration: none;
  color: #007bff;
  transition: color 0.3s;
}

a:hover {
  text-decoration: underline;
  color: #0056b3;
  cursor: pointer;
}

/* Styling for Graph Containers */
.cumulative-graph, .graph-container {
  padding: 20px;
  background-color: #ffffff;
  border-radius: 12px;
  margin-bottom: 20px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.cumulative-graph h2, .graph-container h2 {
  text-align: center;
  color: #333;
  font-family: 'Montserrat', sans-serif;
}

/* Responsive Styles for Mobile */
@media only screen and (max-width: 768px) {
  .App {
    padding: 10px;
  }

  /* Adjust table, header, and cell styles for mobile view */
  table, th, td {
    font-size: 14px;
    padding: 8px 4px;
  }

  th, td {
    white-space: nowrap;
  }

  /* Flex layout for view-toggle buttons */
  .view-toggle {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
  }

  .view-toggle button {
    flex: 1 1 45%;
    margin: 5px;
    padding: 10px;
    font-size: 14px;
  }

  table {
    display: block;
    overflow-x: auto;
    white-space: nowrap;
  }

  /* Keep table headers visible */
  table thead {
    display: table-header-group;
  }

  /* Maintain row-based structure in mobile view */
  table tbody {
    display: table-row-group;
  }

  /* Ensure columns are correctly displayed */
  table tr {
    display: table-row;
  }

  table tr td {
    display: table-cell;
    padding: 8px;
  }

  /* Improve graph display for mobile */
  .cumulative-graph, .graph-container {
    padding: 15px;
    box-shadow: none; /* Simplify for mobile */
    margin-bottom: 15px;
    width: 100%;
    overflow-x: auto;
  }

  /* Adjust graph height for mobile portrait mode */
  .graph-container canvas {
    height: 50vh !important;
    max-height: 50vh;
  }

  /* Ensure tables and charts fit within the mobile view */
  .graph-container {
    overflow-x: auto;
    white-space: nowrap;
  }

  .summary-table, .graph-container table {
    min-width: 600px; /* Minimum width to ensure the layout is not squished */
  }
}
